html, body {
	margin: 0;
	padding: 0;
}
body {
	padding: 10px;
	font-family: 'Times New Roman', serif;
	font-size: 16px;
	line-height: 20px;
}
ul {
	margin: 20px 0;
	padding: 0;
	list-style: none;
}
h1 {
	margin: 36px 0;
	font-size: 36px;
	line-height: 42px;
}
h2 {
	margin: 24px 0;
	font-size: 24px;
	line-height: 28px;
}
h3 {
	margin: 18px 0;
	font-size: 18px;
	line-height: 20px;
}